export default {
	data() {
		return {
			userInfo: {}
		}
	},
	methods: {
		userMsg() {
			uni.getUserInfo({
				success: (data) => {
					console.log(data, '111');
					this.userInfo = data.userInfo
					console.log(this.userInfo, '---this.userinfo');
				},
				fail: (data) => {
					console.log(data, "获取用户信息失败");
				},
				complete: () => {
					console.log("complete");
				}
			})
		},
		getUserInfo(data) {
			console.log(data, '---111data');
			if(data.mp.detail.rawData) {
				this.userMsg()
			}
		}
	}
	
	
}
